
import {setTitle} from './../../assets/js/common.js'
import { Button } from 'mint-ui';
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Countdown from 'vux/src/components/countdown/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'

import VueCoreImageUpload from 'vue-core-image-upload'
export default {
name: 'index',
data (){
return {
toast_show:false,
toast_text:'',
user:'', // 用户表
doctor:'', // 治疗师表
}
},
created(){

var store_arr=[
'user',
'doctor',
];
for(var i in store_arr){
this[store_arr[i]]=this.$store.state[store_arr[i]];
};

},
components:{
'mtButton':Button,
Group,
XInput,
Countdown,
Toast,
Datetime,
VueCoreImageUpload
},
watch:{
'$route':function(){
console.log(this.$route.path=='/app/login')
}
},
methods:{
change (value) {
this.user.birth=value;
console.log('change', value);
},
next_fn(){
this.$store.commit('user_up',this.user);
console.log(this.$store.state.user);
this.$store.commit('doctor_up',this.doctor);
console.log(this.$store.state.doctor);
},
user_sex_fn(select){
this.user.sex=select;
},
imageuploaded(res) { // 定义上传完成执行的方法
console.log(res)
},
}
}




